@import url(https://fonts.googleapis.com/css2?family=Cairo&display=swap);
body {
  background-color: grey;
}

.weather-app {
  height: 100vh;
  padding: 1em;
}

body {
  font-family: "Cairo", sans-serif;
  margin: 0;
}



.location-details {
  display: flex;
  justify-content: center;
  font-family: Montserrat;
}

.forecast-summary {
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 5px;
  background-color: rgba(255, 255, 255, 0.7);
  font-size: 0.8rem;
  width: 16%;
  text-align: center;
}

.forecast-summary > button {
  border: none;
  font-size: 0.75rem;
  padding: 0px;
  margin: 5px;
  background-color: rgba(255, 255, 255, 0);
  font-family: sans-serif;
  outline: none;
  cursor: pointer;
}

.forecast-summary > div {
  margin: 3px;
}

.forecast-summary__icon {
  font-size: 35px;
  padding: px;
}

@media only screen and (max-width: 750px) {
  .forecast-summary {
    width: 16%;
    /* padding: 5px 4px; */
  }

  .forecast-summary > button {
    font-size: 0.5rem;
    margin: 1px;
  }

  .forecast-summary > div {
    margin: 1px;
  }

  .forecast-summary__icon {
    font-size: 20px;
    padding: 5px;
  }
}

.forecast-summaries {
  display: flex;
  justify-content: space-around;
  margin: 30px 10px;
}

.forecast-details {
  display: flex; /* establish flex container */
  flex-direction: column; /* make main axis vertical */
  align-items: center; /* center items horizontally, in this case */
  padding-top: 25px;
}

.forecast-details__date {
  font-weight: bold;
  font-size: 25px;
}

